import clone from '@/lib/clone'
import createId from '@/lib/createId'
import router from '@/router'
import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)

const store = new Vuex.Store({
  state: {
    recordList: [],
    createRecordError:null,
    createTagError: null,
    tagList:[],
    currentTag: undefined
  } as RootState,
  mutations:{
    fetchRecord(state){
      state.recordList = JSON.parse(window.localStorage.getItem('recordList')||'[]')
    },
    createRecord (state,record){
      const record2 = clone(record); //
      record2.createdAt = new Date().toISOString()
      state.recordList.push(record2)
      store.commit('saveRecords')
    },
    saveRecords(state){
      window.localStorage.setItem('recordList', JSON.stringify(state.recordList))
    },
    //findTagmutationscurrentTag
    setCurrentTag(state,id:string){
       state.currentTag = state.tagList.filter((t) => t.id === id)[0];
    },
    fetchTags(state) {
      state.tagList = JSON.parse(window.localStorage.getItem('tagList') || '[]');
      if(!state.tagList || state.tagList.length === 0) {
        store.commit('createTag', '');
        store.commit('createTag', '');
        store.commit('createTag', '');
        store.commit('createTag', '');
      }
    },
    createTag(state,name: string) {
      state.createTagError = null;
      const names = state.tagList.map(item => item.name)  //data={[id:"1",name:"1"]}name
      if (names.indexOf(name) >= 0) {
        state.createTagError = new Error("Tag name duplicated")
        return;
      };
      const id = createId().toString();
      state.tagList.push({ id, name: name });
      store.commit('saveTag');
    },
    saveTag(state) {
      window.localStorage.setItem('tagList', JSON.stringify(state.tagList))
    },
    removeTag(state,id: string) {
      let index = -1;
      for (let i = 0; i < state.tagList.length; i++) {
        if (state.tagList[i].id === id) {
          index = i
          break;
        }
      }
      if(index >= 0) { 
        state.tagList.splice(index, 1)
        store.commit('saveTag');
        router.back();
      }else {
        window.alert("")
      }
    },
    updateTag(state,object: {id:string, name:string}) {
      const {id,name} = object;
      const idList = state.tagList.map((item: { id: any }) => item.id);
      if (idList.indexOf(id) >= 0) {
        const names = state.tagList.map((item: { name: any }) => item.name);
        if (names.indexOf(name) >= 0) {
          window.alert("")
        } else {
          const tag = state.tagList.filter((item: { id: string }) => item.id === id)[0]
          tag.name = name;
          store.commit('saveTag');
        }
      } 
    }
  }
})
export default store;
